import React from "react";
import Home from "./pages/Home";
import { Route, Routes, useLocation } from "react-router-dom";
import { AnimatePresence } from "../node_modules/framer-motion/dist/framer-motion";
const Navi = React.lazy(() => import("./pages/Navi"));
const Ksos = React.lazy(() => import("./pages/Ksos"));
const Agent = React.lazy(() => import("./pages/Agent"));

export default function AnimatedRoutes() {
  const location = useLocation();
  return (
    <AnimatePresence>
      <Routes location={location} key={location.pathname}>
        <Route path="/app" element={<Home />} />
        <Route
          path="/Navi"
          element={
            <React.Suspense>
              <Navi />
            </React.Suspense>
          }
        />
        <Route
          path="/Ksos"
          element={
            <React.Suspense>
              <Ksos />
            </React.Suspense>
          }
        />
        <Route
          path="/Agent"
          element={
            <React.Suspense>
              <Agent />
            </React.Suspense>
          }
        />
      </Routes>
    </AnimatePresence>
  );
}
